/* ========== Class from the main html ========== */
.dropdown {
  width: 100%;
  position: inherit;
  overflow-x: hidden; /* Esto puede ayudar a ocultar el contenido que se sale del contenedor */
}
@media (min-width: 992px) {
  .dropdown {
    width: auto;
  }
}

.dropdown-content {
  position: absolute;
  /* background-color: #324550; */
  min-width: 220px;
  right: 0;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.7s ease-in-out, transform 0.7s ease-in-out, height 0.1s ease-in-out; /* Agregamos una transición también para el transform */
  transform: translateY(-20px); /* Iniciamos el elemento un poco arriba para que se mueva suavemente hacia abajo */
  overflow-x: hidden; /* Esto puede ayudar a ocultar el contenido que se sale del contenedor */
}
@media (min-width: 992px) {
  .dropdown-content {
    display: flex;
    position: initial;
    transform: none;
    visibility: visible;
    opacity: 1;
  }
}

.navegacion-header {
  transition: opacity 0.7s ease-in-out, transform 0.7s ease-in-out, height 0.1s ease-in-out; /* Agregamos una transición también para el transform */
  transform: translateY(-20px); /* Iniciamos el elemento un poco arriba para que se mueva suavemente hacia abajo */
  overflow-x: hidden; /* Esto puede ayudar a ocultar el contenido que se sale del contenedor */
  /* margin-top: 3rem; */
}
.navegacion-header a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 300;
  display: block; /* vista para mmovil */
  text-align: center;
}
@media (min-width: 992px) {
  .navegacion-header {
    display: flex;
    gap: 2.5rem;
    margin: 0rem;
    transform: none;
    visibility: visible;
    opacity: 1;
  }
}

/*=============== Menú de hamburgesa ===============*/
.mobile-menu {
  cursor: pointer;
}
@media (min-width: 992px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 3.5rem;
}
@media (min-width: 768px) {
  .mobile-menu img {
    width: 4rem;
  }
}

.mostrar {
  background-color: #324550;
  visibility: visible;
  opacity: 1;
  height: auto;
  transform: translateY(0); /* Cuando se muestra, lo movemos hacia abajo */
  overflow-x: hidden; /* Esto puede ayudar a ocultar el contenido que se sale del contenedor */
  /* @include desktop {
      display: none; ejemplo si puede eliminar las propiedades de mostrar
  } */
}
.mostrar a {
  margin: 3rem 0rem;
  text-align: right;
  padding-right: 2rem;
}

/*=============== User - Sidebar ===============*/
.dropdown-user {
  position: relative;
}

.siderbar-user {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 190px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  left: -74px;
  margin-top: 1rem;
}
@media (min-width: 992px) {
  .siderbar-user {
    right: 0;
    left: auto;
    margin-top: 0rem;
  }
}
